package eu.karelhovorka.util

fun String.fixBOM(): String {
    return this.replace("\uFEFF", "")
}

fun String.removeAccents(): String {
    var result = this
    val sdiak = "áäčďéěíĺľňóô öŕšťúů üýřžÁÄČĎÉĚÍĹĽŇÓÔ ÖŔŠŤÚŮ ÜÝŘŽ"
    val bdiak = "aacdeeillnoo orstuu uyrzAACDEEILLNOO ORSTUU UYRZ"
    for (index in 0 until sdiak.length) {
        result = result.replace(sdiak[index], bdiak[index])
    }
    result = result.replace("ě", "e")
    return result
}

fun String.simplify(): String {
    val result = this.removeAccents().replace("(\\s|[, -.!?])*".toRegex(), "")
    return result

}

fun String.startSharedWith(finalEncrypted: String): Int {
    val maxSize = MathUtil.min(length, finalEncrypted.length)
    for (index in 0..maxSize ) {
        if (finalEncrypted[index] != this[index]) {
            return index
        }
    }
    return maxSize
}