package eu.karelhovorka.util.html

import org.w3c.dom.HTMLElement
import org.w3c.dom.asList
import kotlin.browser.document


fun HTMLElement.hide() {
    this.style.display = "none"
}

fun HTMLElement.show(display: String = "block") {
    this.style.display = display
}

fun HTMLElement.newParent(elementName: String = "div"): HTMLElement {
    val newDiv = document.createElement(elementName) as HTMLElement
    while (this.hasChildNodes()) {
        newDiv.appendChild(this.firstChild!!)
    }
    this.appendChild(newDiv)
    return newDiv
}

fun HTMLElement.primarecFix(): HTMLElement {
    val elements = this.querySelectorAll("primarec, myslenka").asList()
    for (node in elements) {
        val element = node as HTMLElement
        val newParent = element.newParent()
        newParent.classList.add("inner-bubble")
    }
    return this
}