package eu.karelhovorka.util

class CyclicListIterator<T>(private val array: List<T>, initialIndex: Int = 0) : ListIterator<T> {

    private var currentIndex = initialIndex

    fun randomize(): CyclicListIterator<T> {
        currentIndex = RandomUtil.nextInt(array.size)
        return this
    }

    fun current(): T {
        return array[currentIndex]
    }

    override fun hasNext(): Boolean {
        return true
    }

    override fun hasPrevious(): Boolean {
        return true
    }

    override fun next(): T {
        currentIndex = nextIndex()
        return array[currentIndex]
    }

    override fun nextIndex(): Int {
        var nextIndex = currentIndex + 1
        if (nextIndex >= array.size) {
            nextIndex = 0
        }
        return nextIndex
    }

    override fun previous(): T {
        currentIndex = previousIndex()
        return array[currentIndex]
    }

    override fun previousIndex(): Int {
        var previousIndex = currentIndex - 1
        if (previousIndex < 0) {
            previousIndex = array.size - 1
        }
        return previousIndex
    }

}